@mixin desktop-landscape {
  //--|🠊 display: flex; 🠈|--//
  display: inherit; //~~|🠊 display: grid; 🠈|~~//
  border-radius: 1rem;
  grid-area: 2 / 3 / 3 / 4; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//
  background: rgba(black, 0.75);
  grid-template: 1fr auto 1fr / 1fr; //~~|🠊 rows / columns 🠈|~~//
  div[class*='header'] {
    display: inherit; //~~|🠊 display: grid; 🠈|~~//
    grid-area: 1 / 1 / 2 / 2; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//
    grid-template: 4rem 1fr / 4rem 1fr 4rem; //~~|🠊 rows / columns 🠈|~~//
    // background: rgba(red, 0.25);
    div[class*='logo'] {
      display: flex;
      justify-content: center;
      grid-area: 1 / 1 / 2 / 2; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//
      img {
        width: 100%;
        margin: 0.5rem;
      }
    }
    div[class*='label'] {
      grid-area: 1 / 2 / 2 / 3;
      h6 {
        margin: 0;
        width: 100%;
        color: white;
        padding: 0.5rem;
        text-align: center; //~~|🠊 Ensures Text is Centered 🠈|~~//
      }
    }
    button[class*='close'] {
      background: none;
      grid-area: 1 / 3 / 2 / 4; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//
      img {
        width: 3rem;
        height: 3rem;
        padding: 0.5rem;
      }
    }
    div[class*='text'] {
      display: flex;
      align-items: center;
      grid-area: 2 / 2 / 3 / 3;
      h4 {
        padding: 4px;
        line-clamp: 3; //~~|🠊 Standard property 🠈|~~//
        color: white;
        text-wrap: balance;
        text-align: center; //~~|🠊 Justifies text alignment 🠈|~~//
        display: -webkit-box; //~~|🠊 Enables multi-line truncation 🠈|~~//
        -webkit-line-clamp: 4; //~~|🠊 Limits the number of lines  🠈|~~//
        text-overflow: ellipsis; //~~|🠊 Adds ellipsis for overflow 🠈|~~//
        -webkit-box-orient: vertical; //~~|🠊 Vertical Text 🠈|~~//
      }
    }
  }
  div[class*='inputs'] {
    display: flex;
    flex-direction: column;
    grid-area: 2 / 1 / 3 / 2; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//

    // background: rgba(green, 0.25);
    input {
      padding: 0.25rem 0.75rem;
      margin: 1rem 2rem;
    }
  }
  div[class*='footer'] {
    display: inherit; //~~|🠊 display: grid; 🠈|~~//
    grid-area: 3 / 1 / 4 / 2; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//
    grid-template: 1fr 1fr / repeat(2, 1fr); //~~|🠊 rows / columns 🠈|~~//
    // background: rgba(blue, 0.25);
    mark[class*='action'] {
      display: inherit; //~~|🠊 display: grid; 🠈|~~//
      grid-area: 1 / 1 / 2 / 3; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//
      grid-template: 1fr auto 1fr / repeat(2, 1fr); //~~|🠊 rows / columns 🠈|~~//
      background: none;
      .login-button {
        grid-area: 2 / 1 / 3 / 2; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//
      }
      div[class*='message'] {
        display: flex;
        align-items: center;
        grid-area: 2 / 2 / 3 / 3; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//

        h6 {
          margin: 0;
          width: 100%;
          color: white;
          text-align: center;

          // background: darkblue;
        }
      }
      button {
        h6 {
          cursor: pointer;
          color: black;
        }
        margin: 1rem;
        justify-content: center;
        padding: 0.75rem 0 0.5rem 0; //~~|🠊 padding: Top Right Bottom Left 🠈|~~//
      }
    }
    menu[class*='buttons'] {
      display: inherit; //~~|🠊 display: grid; 🠈|~~//
      grid-area: 2 / 1 / 3 / 3; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//
      grid-template: 1fr auto 1fr / repeat(2, 1fr); //~~|🠊 rows / columns 🠈|~~//
      // background: blue;
      button[class*='register'] {
        grid-area: 2 / 1 / 3 / 2; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//
      }
      button[class*='password'] {
        grid-area: 2 / 2 / 3 / 3; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//
      }
      button {
        h6 {
          cursor: pointer;
          color: black;
        }
        margin: 1rem;
        background: white;
        justify-content: center;
        padding: 0.75rem 0 0.5rem 0; //~~|🠊 padding: Top Right Bottom Left 🠈|~~//
      }
    }
  }
}
