@mixin desktop-landscape {
  //--|🠋 Desktop (Landscape) 🠋|--//
  @media screen and (orientation: landscape) {
    //--|🠊 display: grid; 🠈|--//
    grid-template: 4rem / auto 1fr; //~~|🠈 rows / columns 🠈|~~//
    .signature-adjacent {
      height: 2rem;
      margin: 1rem 3rem;
    }
    menu {
      grid-area: 1 / 2 / 2 / 3; //~~|🠈 row-start / col-start / row-end / col-end 🠈|~~//
    }
  }
}
@mixin mobile-portrait {
  //--|🠋 Mobile (Portrait) 🠋|--//
  @media screen and (orientation: portrait) {
    grid-template: repeat(3, 1fr) / repeat(3, 1fr); //~~|🠊 rows / columns 🠈|~~//
    .signature-adjacent {
      height: 75%;
      grid-area: 2 / 2 / 3 / 3;
    }
  }
}
header[id*='resume'] {
  display: grid;
  grid-area: 1 / 1 / 2 / 4; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//
  @include mobile-portrait();
  @include desktop-landscape();
}

